import "./styles.css";
import menu from "./mainmenu";
import Dmenu from "./dmenu";

function mainmenus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

function dmenus(index) {
  const items = [];
  const keys = Object.keys(Dmenu[index]);
  const values = Object.values(Dmenu[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

export default function App() {
  return (
    <>
      <h1>メインメニュー</h1>
      {mainmenus(0)}
      {mainmenus(1)}
      {mainmenus(2)}
      {mainmenus(3)}

      <h1>ドリンクメニュー</h1>
      {dmenus(0)}
      {dmenus(1)}
      {dmenus(2)}
      {dmenus(3)}
    </>
  );
}
